*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  border-style: solid;
  width: 100%;
  height: 100vh;
  background-image: url(./imagenes/wallpaperflare.com_wallpaper\ \(1\).jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
}

.contenedor{
  width: 90%;
  height:90%;
  background-color: #1b1b3282;
  box-shadow: 0 0 .5rem black;
  border-radius:.5rem;
  -webkit-border-radius:.5rem;
  -moz-border-radius:.5rem;
  -ms-border-radius:.5rem;
  -o-border-radius:.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

/* --------------------------------------------- */
/* video y texto */

.contenedor__video{
  background-color: rgba(0, 0, 0, 0.508);
  width: 50%;
  height: 90%;
  border-radius: .5rem;
  -webkit-border-radius: .5rem;
  -moz-border-radius: .5rem;
  -ms-border-radius: .5rem;
  -o-border-radius: .5rem;
  display: flex;
  justify-content: center;
  padding: .5rem;
  
  
}

.mi_video{
  width: 40%;
  cursor: pointer;
}

.contenedor__parrafo{
  width: 50%;
  height: 90%;
  border-radius: .5rem;
  background-color: rgba(0, 0, 0, 0.508);
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
}

.contenedor__parrafo--parrafo{
  padding: 1rem;
  color: white;
}



/* ------------------------------------------------ */
/* responsive */

@media screen and (max-width:768px){
  .App {
    height: auto;
  }
  .contenedor{
    width: 95%;
    margin-top: 2rem;
    margin-bottom: 2rem;
    flex-direction: column;
    height: auto;
  }
  .contenedor__video{
    
    margin-top: 1rem;
    min-width: 95%;
    /* height: 45%; */
  }

  .mi_video{
    width: 100%;
    padding: 1rem;
    cursor: pointer;
  }
  .contenedor__parrafo{
    margin-bottom: 1rem;
    width: 95%;
  }
  
}